.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 250%;
		transform: translate(-50%, -50%) scaleX(-1);
	}
	.orn-2 {
		position: absolute;
		top: 5rem;
		left: 0;
		width: 120px;
		transform: translate(-50%, 0);
		@include min-md {
			width: 140px;
		}
		.orn-3 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 180px;
			transform: translate(0, 50%);
			@include min-md {
				width: 220px;
			}
		}
	}
	.orn-4 {
		position: absolute;
		top: 1rem;
		right: 0;
		width: 120px;
		transform: translate(50%, 0) scaleX(-1);
		@include min-md {
			width: 140px;
		}
		.orn-5 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 140px;
			transform: translate(-5%, 70%) scaleX(-1);
			@include min-md {
				width: 160px;
			}
		}
	}
	.orn-6 {
		position: absolute;
		top: 2rem;
		left: 1rem;
		width: 45px;
		transform: rotate(45deg);
		@include min-md {
			width: 60px;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		aspect-ratio: 3/4;
		.rsvp-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 500px;
		}
	}
	.rsvp-wrapper {
		position: relative;
		padding: 3rem 2rem;
		@include max-md {
			padding: 2rem 1rem;
		}
		.rsvp-form {
			position: relative;
			.title-section {
				&::before {
					all: unset;
				}
				h2,
				span,
				p {
					position: relative;
				}
			}
			.form-control,
			.form-select {
				background-color: rgba($color: #fff, $alpha: 1);
				border: 1px solid var(--color-secondary);
				border-radius: 3px;
			}
		}
	}
}
