.reminder-wrap {
	position: relative;
	display: block;
	padding: 2rem 0 5rem;
	overflow: hidden;
	@include max-md {
		padding: 2rem 0;
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 120px;
		transform: translate(-40%, 30%);
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 120px;
		transform: translate(40%, 30%) scaleX(-1);
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.reminder-content {
		position: relative;
		width: fit-content;
		max-width: 100%;
		margin: 0 auto;
		padding: 8rem 5rem;
		border-radius: 500px;
		overflow: hidden;
		@include max-md {
			padding: 5rem 3rem;
		}
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1.5rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
