.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	height: 100vh;
	width: 100%;
	&.cover-opened {
		transform: translateY(-100%);
	}
	.orn-99 {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		transform: translateY(-60%);
		@include max-md {
			transform: translateY(0);
		}
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		transition: opacity 1s ease;
		@include max-lg {
			padding: 4rem 0 8rem;
			align-items: center;
		}
		@media (max-width: 400px) and (max-height: 690px) {
			transform: scale(0.75);
		}
		.cover-content {
			text-align: center;
			position: relative;
			background-color: transparent;
			border-radius: 12px;
			color: var(--color-dark);
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			.cover-body {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				padding: 1rem 0;
				&::before {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					background: rgba($color: #fff, $alpha: 0.8);
					content: "";
					border-radius: 20px;
				}
				@include max-lg {
					flex-direction: column;
					margin-top: 10rem;
					width: 80%;
				}
				@include max-md {
					width: 100%;
				}
				.elements-wrapper-absolute {
					border-radius: 20px;
					overflow: hidden;
				}
				.greeting-wrapper {
					position: relative;
					@include max-md {
						font-size: 14px;
					}
					h6 {
						font-weight: 700;
					}
				}
				h6 {
					font-weight: 600;
				}
				.image-wrapper {
					position: relative;
					width: 240px;
					height: auto;
					aspect-ratio: 7/8;
					@include max-lg {
						width: 60%;
						margin: 0 auto;
						margin-top: -10rem;
						margin-bottom: 2rem;
					}
					@include max-md {
						width: 70%;
						height: 100%;
					}
					@include max-xs {
						width: 60%;
					}
					.image-element {
						position: absolute;
						top: 50%;
						left: 50%;
						width: 130%;
						height: 130%;
						transform: translate(-70%, -50%);
						padding: 20px;
						background-color: rgba($color: #fff, $alpha: 0.8);
						border-radius: 500px 500px 30px 30px;
						overflow: hidden;
						@include max-lg {
							transform: translate(-50%, -50%);
						}
						.cover-background {
							width: 100%;
							height: 100%;
							object-fit: cover;
							border-radius: 500px 500px 30px 30px;
							position: relative;
						}
					}
				}
				.cover-description {
					padding: 0 2rem;
					text-align: center;
					@include max-lg {
						padding: 2rem 0 1rem;
					}
					.title {
						color: var(--color-dark);
						font-size: 32px;
					}
				}
			}
			.elements-widget {
				width: 100%;
			}
		}
	}
}
