.header-section {
	position: relative;
	overflow: hidden;
	width: 100%;
	.scroll-icon {
		position: relative;
		margin-top: 2rem;
	}
	.orn-99 {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		transform: translateY(-60%);
		@include max-md {
			transform: translateY(0);
		}
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 180px;
		transform: translate(-40%, 0);
		@include min-xs {
			width: 220px;
		}
		@include min-md {
			width: 320px;
		}
		@include min-lg {
			transform: translate(-35%, 30%);
		}
		.orn-2 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 140px;
			transform: translate(5%, 55%);
			@include min-xs {
				width: 160px;
			}
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				transform: translate(25%, 15%);
			}
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 140px;
		transform: translate(30%, 0);
		@include min-xs {
			width: 160px;
		}
		@include min-md {
			width: 240px;
		}
		@include min-lg {
			transform: translate(20%, 10%);
		}
		.orn-4 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 140px;
			transform: translate(-15%, 55%) scaleX(-1);
			@include min-xs {
				width: 160px;
			}
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				transform: translate(-15%, 35%) scaleX(-1);
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 18%;
		left: 0;
		width: 120px;
		transform: translate(-30%, 0);
		@include min-xs {
			width: 140px;
		}
		@include min-md {
			bottom: 20%;
			width: 180px;
			transform: translate(0, 0);
		}
		.orn-6 {
			position: absolute;
			top: 0;
			right: 0;
			width: 120px;
			transform: translate(45%, -10%) rotate(10deg);
			@include min-xs {
				width: 140px;
				transform: translate(50%, -20%) rotate(5deg);
			}
			@include min-md {
				width: 180px;
			}
			@include min-lg {
				transform: translate(50%, -10%) rotate(5deg);
			}
			.image-element {
				opacity: 0.8;
			}
			.orn-9 {
				.image-element {
					opacity: 1;
				}
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 20%;
		right: 0;
		width: 140px;
		transform: translate(50%, 0);
		opacity: 0.8;
		@include min-xs {
			transform: translate(40%, 0);
			width: 200px;
		}
		@include min-md {
			bottom: 22%;
			width: 260px;
		}
		.orn-8 {
			position: absolute;
			top: 0;
			left: 0;
			width: 80px;
			transform: translate(-65%, -10%);
			@include min-xs {
				width: 100px;
				transform: translate(-65%, 0);
			}
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				transform: translate(-80%, 10%);
			}
			.image-element {
				opacity: 0.6;
			}
			.orn-9 {
				.image-element {
					opacity: 1;
				}
			}
		}
	}
	.orn-9 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 120px;
		transform: translate(-50%, 0);
		@include min-md {
			width: 180px;
		}
	}
	.orn-10 {
		position: absolute;
		bottom: 23%;
		left: 28%;
		width: 100px;
		transform: translate(-50%, 50%);
		@include min-lg {
			left: 35%;
			width: 120px;
		}
	}
	.orn-11 {
		position: absolute;
		bottom: 13%;
		right: 28%;
		width: 100px;
		transform: translate(50%, 50%) scaleX(-1);
		@include min-lg {
			right: 35%;
			width: 120px;
		}
	}
	.header-content {
		position: relative;
		height: 100vh;
		.header {
			position: relative;
			width: 50%;
			height: 100%;
			margin: 0 auto;
			padding: 8rem 0 0;
			@include max-md {
				width: 90%;
			}
			.header-title {
				position: relative;
				text-align: center;
				&-content {
					position: relative;
					border-radius: var(--border-radius);
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes zoomInMain {
	0% {
		transform: scaleZ(1);
	}
	100% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
}

@keyframes zoomInMain {
	0% {
		transform: scaleZ(1);
	}
	100% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
