.couple-section {
	position: relative;
	text-align: center;
	.and {
		@include min-md {
			display: none;
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: fit-content;
		position: relative;
		margin: 2rem auto;
		z-index: 3;
		span {
			position: relative;
			font-size: 36px;
			font-family: $font-secondary;
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
	}
	.couple-wrapper {
		position: relative;
		padding: 12rem 0 5rem;
		@include max-md {
			padding: 5rem 0 3rem;
		}
		.orn-2 {
			position: absolute;
			top: 0;
			left: 0;
			width: 140px;
			transform: translate(-60%, 0);
			opacity: 0.8;
			@include min-md {
				width: 180px;
				transform: translate(-50%, 0);
			}
			.orn-3 {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 160px;
				transform: translate(-20%, 50%);
				opacity: 1;
				@include min-md {
					width: 240px;
				}
			}
		}
		.orn-4 {
			position: absolute;
			top: 0;
			right: 0;
			width: 130px;
			transform: translate(50%, 0) scaleX(-1);
			@include min-md {
				width: 160px;
			}
			.orn-5 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 140px;
				transform: translate(-15%, 70%);
				@include min-md {
					width: 180px;
				}
			}
		}
		.orn-6 {
			position: absolute;
			top: 55%;
			left: 0;
			width: 180px;
			transform: translate(-55%, 0);
			@include min-md {
				width: 220px;
			}
			@include min-lg {
				top: 60%;
			}
			.orn-7 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 100px;
				transform: translate(-20%, 30%);
				@include min-md {
					width: 120px;
				}
			}
		}
		.orn-8 {
			position: absolute;
			top: 55%;
			right: 0;
			width: 120px;
			transform: translate(50%, 0) scaleX(-1);
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				top: 60%;
			}
			.orn-9 {
				position: absolute;
				right: 0;
				bottom: 0;
				width: 160px;
				transform: translate(0, 50%);
				@include min-md {
					width: 200px;
				}
			}
		}
		.orn-10 {
			position: absolute;
			top: 50%;
			left: 50%;
			width: 240%;
			transform: translate(-50%, -50%) scaleX(-1);
		}
	}
	.couple-body {
		position: relative;
	}
	.couple-content {
		position: relative;
		padding: 0 1rem;
	}
	.couple {
		position: relative;
		text-align: center;
		border-radius: 500px 500px 20px 20px;
		box-shadow: 0 0 1rem rgba($color: #000000, $alpha: 0.15);
		padding: 2rem;
		margin-bottom: 1rem;
		.elements-wrapper-absolute {
			border-radius: 500px 500px 20px 20px;
			overflow: hidden;
		}
		.image-wrap {
			width: 100%;
			position: relative;
			aspect-ratio: 7/8;
			margin: 0 auto;
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					border-radius: 500px 500px 20px 20px;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 1rem 1rem 0;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: absolute;
			bottom: -3rem;
			left: 50%;
			transform: translate(-50%, 50%);
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			margin: 0 auto;
			.sosmed {
				color: var(--text-button);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
