.stream-section {
	position: relative;
	padding: 12rem 0 5rem;
	text-align: center;
	@include max-lg {
		padding: 8rem 0 5rem;
	}
	@include max-md {
		padding: 5rem 0 3rem;
	}
	.orn-1 {
		position: absolute;
		top: 2rem;
		right: 3rem;
		width: 45px;
		transform: rotate(-45deg);
		@include min-md {
			top: 5rem;
			right: 5rem;
			width: 60px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 50%;
		right: 0;
		width: 120px;
		transform: translate(60%, -50%) scaleX(-1);
		@include min-md {
			width: 180px;
			transform: translate(50%, -50%) scaleX(-1);
		}
		.orn-3 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 180px;
			transform: translate(0, 50%);
			@include min-md {
				width: 240px;
			}
		}
	}
	.orn-4 {
		position: absolute;
		top: 3rem;
		left: 0;
		width: 120px;
		transform: translate(-50%, 0);
		@include min-md {
			width: 140px;
		}
		.orn-5 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 140px;
			transform: translate(-5%, 70%);
			@include min-md {
				width: 180px;
			}
		}
	}
	.stream-wrapper {
		position: relative;
		border-radius: var(--border-radius);
		z-index: 5;
		@include max-lg {
			.row {
				align-items: center;
				gap: 2rem;
			}
		}
		@include max-md {
			padding: 2rem 1rem;
		}
		.stream-content {
			position: relative;
			.stream-description {
				position: relative;
				padding: 3rem 2rem;
				&::before {
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
					background: var(--color-main);
					opacity: 0.6;
					border-radius: 20px 20px 0 0;
					content: "";
				}
				@include max-md {
					padding: 2rem 1rem;
				}
			}
			h4 {
				@include max-md {
					text-align: center;
				}
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.card {
		background-color: transparent;
		height: 100%;
		color: var(--color-dark);
		border: none;
	}
	.btn-custom {
		width: 100%;
		z-index: 10;
		position: relative;
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		position: relative;
		width: 100%;
		@include max-md {
			text-align: center;
		}
	}
}
