.apology-section {
	position: relative;
	background: var(--color-primary);
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 1rem;
		right: 3rem;
		width: 45px;
		transform: rotate(-45deg);
		@include min-md {
			width: 60px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 1rem;
		right: 0;
		width: 120px;
		transform: translate(60%, 0) scaleX(-1);
		@include min-md {
			width: 180px;
			transform: translate(50%, 0) scaleX(-1);
		}
		.orn-3 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 180px;
			transform: translate(0, 50%);
			@include min-md {
				width: 240px;
			}
		}
	}
	.orn-4 {
		position: absolute;
		top: 0;
		left: 0;
		width: 120px;
		transform: translate(-50%, 0);
		@include min-md {
			width: 140px;
		}
		.orn-5 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 140px;
			transform: translate(-5%, 70%);
			@include min-md {
				width: 180px;
			}
		}
	}
	.appology-wrap {
		position: relative;
		padding: 5rem 2rem 3rem;
		border-radius: 500px 500px 20px 20px;
		overflow: hidden;
		@include max-md {
			padding: 4rem 1rem 2rem;
		}
	}
}
