.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 2rem 0 3rem;
	}
	.elements-wrapper-absolute {
		border-radius: 1000px 1000px 0 0;
		overflow: hidden;
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 180px;
		transform: translate(-40%, 0);
		@include min-xs {
			width: 240px;
		}
		@include min-md {
			width: 320px;
		}
		@include min-lg {
			transform: translate(-35%, 30%);
		}
		.orn-2 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 140px;
			transform: translate(5%, 55%);
			@include min-xs {
				width: 180px;
			}
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				transform: translate(25%, 15%);
			}
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 140px;
		transform: translate(30%, 0);
		@include min-xs {
			width: 180px;
		}
		@include min-md {
			width: 240px;
		}
		@include min-lg {
			transform: translate(20%, 10%);
		}
		.orn-4 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 140px;
			transform: translate(-15%, 55%) scaleX(-1);
			@include min-xs {
				width: 180px;
			}
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				transform: translate(-15%, 35%) scaleX(-1);
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 18%;
		left: 0;
		width: 120px;
		transform: translate(-30%, 0);
		@include min-xs {
			width: 140px;
		}
		@include min-md {
			bottom: 20%;
			width: 180px;
			transform: translate(0, 0);
		}
		.orn-6 {
			position: absolute;
			top: 0;
			right: 0;
			width: 120px;
			transform: translate(45%, -10%) rotate(10deg);
			@include min-xs {
				width: 160px;
				transform: translate(50%, -20%) rotate(5deg);
			}
			@include min-md {
				width: 180px;
			}
			@include min-lg {
				transform: translate(50%, -10%) rotate(5deg);
			}
			.image-element {
				opacity: 0.8;
			}
			.orn-9 {
				.image-element {
					opacity: 1;
				}
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 20%;
		right: 0;
		width: 140px;
		transform: translate(50%, 0);
		opacity: 0.8;
		@include min-xs {
			transform: translate(40%, 0);
			width: 200px;
		}
		@include min-md {
			bottom: 22%;
			width: 260px;
		}
		.orn-8 {
			position: absolute;
			top: 0;
			left: 0;
			width: 80px;
			transform: translate(-65%, -10%);
			@include min-xs {
				width: 120px;
				transform: translate(-70%, -10%);
			}
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				transform: translate(-80%, 10%);
			}
			.image-element {
				opacity: 0.6;
			}
			.orn-9 {
				.image-element {
					opacity: 1;
				}
			}
		}
	}
	.orn-9 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 120px;
		transform: translate(-50%, 0);
		@include min-md {
			width: 180px;
		}
	}
	.orn-10 {
		position: absolute;
		bottom: 23%;
		left: 28%;
		width: 100px;
		transform: translate(-50%, 50%);
		@include min-lg {
			left: 35%;
			width: 120px;
		}
	}
	.orn-11 {
		position: absolute;
		bottom: 13%;
		right: 28%;
		width: 100px;
		transform: translate(50%, 50%) scaleX(-1);
		@include min-lg {
			right: 35%;
			width: 120px;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 7/8;
		margin: 0 auto;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 500px 500px 20px 20px;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background: var(--color-main);
			opacity: 0.6;
			content: "";
			border-radius: 500px 500px 20px 20px;
		}
		.thank {
			position: relative;
			padding: 3rem 2rem;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
